module.exports = "/**\n * Applies styles for users in high contrast mode. Note that this only applies\n * to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n * attribute, however Chrome handles high contrast differently.\n * @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n *    `white-on-black` or `black-on-white`.\n */\n/* Theme for the ripple elements.*/\n/* stylelint-disable material/no-prefixes */\n/* stylelint-enable */\nexample-viewer {\n  display: block;\n  padding: 24px 0; }\nexample-viewer .example-viewer-wrapper {\n    border: 1px solid rgba(0, 0, 0, 0.03);\n    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.24), 0 0 2px rgba(0, 0, 0, 0.12);\n    margin: 4px; }\nexample-viewer .example-viewer-wrapper h3 {\n      margin-top: 10px; }\nexample-viewer .example-viewer-header {\n    align-content: center;\n    align-items: center;\n    display: flex;\n    justify-content: center;\n    padding: 8px 20px;\n    color: rgba(0, 0, 0, 0.54);\n    background: rgba(0, 0, 0, 0.03);\n    flex: 1 1 auto; }\nexample-viewer .example-viewer-header .example-viewer-title {\n      flex: 1 1 auto; }\nexample-viewer .example-viewer-source .tab-content {\n    background: #263238; }\nexample-viewer .example-viewer-source .tab-content .tab {\n      position: relative; }\nexample-viewer .example-viewer-source .tab-content .tab .example-source-copy {\n        position: absolute;\n        top: 8px;\n        display: none;\n        right: 8px; }\nexample-viewer .example-viewer-source .tab-content .tab .example-source-copy mat-icon {\n          color: rgba(255, 255, 255, 0.87); }\nexample-viewer .example-viewer-source .tab-content .tab:hover .example-source-copy {\n        display: inline-block; }\nexample-viewer .example-viewer-source .tab-content .tab .example-source {\n        display: flex;\n        padding: 0;\n        margin: 0;\n        min-height: 150px;\n        border-bottom: 1px solid rgba(0, 0, 0, 0.12); }\nexample-viewer .example-viewer-source .tab-content .tab .example-source > pre {\n          width: 100%; }\nexample-viewer .example-viewer-body {\n    padding: 24px; }\n"